import {
  EventEmitter, Directive,
  OnInit, OnDestroy, Output, AfterViewInit, NgModule
} from '@angular/core';

@Directive({
  selector: '[adaptLifecycleHooks]',
  exportAs: 'adaptLifecycleHooks',
})
export class AdaptLifecycleHooksDirective implements OnInit, AfterViewInit, OnDestroy {

  /* eslint-disable-next-line @angular-eslint/no-output-on-prefix */
  @Output() readonly onInit: EventEmitter<void> = new EventEmitter();
  @Output() readonly afterViewInit: EventEmitter<void> = new EventEmitter();
  /* eslint-disable-next-line @angular-eslint/no-output-on-prefix */
  @Output() readonly onDestroy: EventEmitter<void> = new EventEmitter();

  ngOnInit(): void {
    this.onInit.emit();
  }

  ngAfterViewInit(): void {
    this.afterViewInit.emit();
  }

  ngOnDestroy(): void {
    this.onDestroy.emit();
  }
}

@NgModule({
  declarations: [AdaptLifecycleHooksDirective],
  exports: [AdaptLifecycleHooksDirective],
})
export class AdaptLifecycleHooksDirectiveModule { }
